import React from 'react';
import './App.css';
import {
    Panel,
    View,
    Root, Header, PanelHeader,ModalRoot, ModalPage, ModalPageHeader, PanelHeaderButton, PanelHeaderBack, Div, SimpleCell, Avatar, Alert, Snackbar
} from "@vkontakte/vkui";
import '@vkontakte/vkui/dist/vkui.css';
import fire from './functions/firebase';
import checkUserExists from './functions/checkUserExist';
import bridge from '@vkontakte/vk-bridge';
import $ from 'jquery';
import getParams from './functions/getParams';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import pivo from './img/pv.png';
import warehouse from './img/wh.png';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Error from '@vkontakte/icons/dist/24/error';


class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            activeView: 'main',
            activePanel: 'main',
            VK_UID: 0,
            userData: {
                id: 0,
                warPoints: 0
            },
            popout: '',
            modal: '',
            snackbar: '',
            activeTab: 0,
            activeTabOwner: {
                first_name: 'Мужик',
                last_name: 'Таджик',
                photo_200: 'https://sun9-60.userapi.com/c849532/v849532674/13d845/_Rsu5onfz-c.jpg'
            },
            points: [
                {
                    name: 'Пятачок',
                    value: 5,
                    lat: 54.791769,
                    lon: 56.086817,
                    owner: 0,
                    background: 'https://firebasestorage.googleapis.com/v0/b/game1-cae49.appspot.com/o/inorsBG%2F%D0%BF%D1%8F%D1%82%D0%B0%D0%BA.png?alt=media&token=e27c9707-bdb0-4dcf-96ec-586d00fea6eb',
                    description: 'ауе'
                }
                // {
                //     name: 'Сахар',
                //     value: 5,
                //     lat: 54.778748,
                //     lon: 56.123970,
                //     owner: 0,
                //     background: 'https://firebasestorage.googleapis.com/v0/b/game1-cae49.appspot.com/o/inorsBG%2F%D1%81%D0%B0%D1%85%D0%B0%D1%80.png?alt=media&token=7b51e43e-16b3-4dc3-aaad-24ebb8caaad3',
                //     description: 'Сижки поштучно'
                // }
            ]
        };
        this.openPanel = this.openPanel.bind(this);
        this.markerClick = this.markerClick.bind(this);
        this.openCaptureModal = this.openCaptureModal.bind(this);
        this.kupitRabotyag = this.kupitRabotyag.bind(this);
    }
    componentDidMount() {
        this.setState({VK_UID: getParams('vk_user_id')});
        bridge.send("VKWebAppInit", {}).then(() => console.log('ВК ПОДКЛЮЧЕН'));
        checkUserExists(getParams('vk_user_id')).then(
            () => {
                fire.firestore().collection('users').doc(getParams('vk_user_id').toString()).onSnapshot(listener => {
                    this.setState({
                        userData: listener.data()
                    });
                });
                fire.firestore().collection('data').doc('points').onSnapshot(listener => {
                    if(listener.data()){
                        this.setState({
                            points: listener.data().main
                        });
                    }
                });
            }
        );
    }
    componentWillUnmount() {

    }
    kupitRabotyag(){
        const snackConfirmed = <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={{backgroundColor: 'var(--accent)'}}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
        >
            Работяги теперь доставляют певко вам
        </Snackbar>;
        const snackDeclined = <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={{backgroundColor: 'var(--field_error_border)'}}><Icon24Error fill="#fff" width={14} height={14} /></Avatar>}
        >
            Недостаточно певка для переманивания работяг
        </Snackbar>;
        this.setState({popout:
                <Alert
                    actions={[{
                        title: 'Отмена',
                        autoclose: true,
                        mode: 'cancel'
                    }, {
                        title: 'Перекупить',
                        autoclose: true,
                        action: () => {
                            if(this.state.userData.warPoints - this.state.points[this.state.activeTab].value >= 0){
                                this.setState({snackbar: snackConfirmed})
                            }
                            else{
                                this.setState({snackbar: snackDeclined})
                            }
                        }
                    }]}
                    onClose={() => {this.setState({popout: null})}}
                >
                    <h2>Подтвердите действие</h2>
                    <p>Перекупить работяг за {this.state.points[this.state.activeTab].value} пива.</p>
                </Alert>
        })
    }
    openPanel(panel){
        this.setState({
            activePanel: panel
        })
    }
    openView(view){
        this.setState({
            activeView: view
        })
    }
    markerClick(props, marker, e){
        if(props.name === "Склад певка"){
            alert('Собрано певка');
            return 0;
        }
        $('#layout').addClass('active');
        bridge.send("VKWebAppGetUserInfo", {}).then(
            v => this.setState({
                activeTab: props.num,
                activeTabOwner: v
            })
        );
    }
    removeIcons(mapProps, map){
        const style =
            [
                {
                    "elementType": "labels",
                    "stylers": [
                        {
                            "visibility": "off"
                        }
                    ]
                },
                {
                    "featureType": "administrative.land_parcel",
                    "stylers": [
                        {
                            "visibility": "off"
                        }
                    ]
                },
                {
                    "featureType": "administrative.neighborhood",
                    "stylers": [
                        {
                            "visibility": "off"
                        }
                    ]
                }
            ];
        map.setOptions({
            styles: style
        });
    }
    openCaptureModal(data){

    }

    render() {
        return (
            <Root activeView={this.state.activeView}>
                <View popout={this.state.popout} id="main" activePanel={this.state.activePanel}>
                    <Panel id="main">
                        <PanelHeader>
                            ВОЙНА ЗА ИНОРС
                        </PanelHeader>
                            {/*<span>{this.state.userData.warPoints}</span>*/}
                        <Map
                            onReady={(mapProps, map) => this.removeIcons(mapProps, map)}
                            style={{width: '100%', height: 'calc(100vh - 57px)'}}
                            google={this.props.google}
                            zoom={14}
                            streetViewControl={false}
                            mapTypeControl={false}
                            scaleControl={false}
                            fullscreenControl={false}
                            zoomControl={false}
                            initialCenter={{
                                lat: 54.779270,
                                lng: 56.123964
                            }}
                        >
                            <Marker
                                num={1488}
                                title="Склад певка"
                                name="Склад певка"
                                background=""
                                description=""
                                onClick={this.markerClick}
                                position={
                                    {lat: 54.777251,
                                        lng: 56.127483}
                                }
                                icon={{
                                    url: warehouse,
                                    // eslint-disable-next-line no-undef
                                    anchor: new google.maps.Point(32,32),
                                    // eslint-disable-next-line no-undef
                                    scaledSize: new google.maps.Size(32,32)
                                }}
                            />
                            {this.state.points.map(value =>
                                <Marker
                                    key={value.name+value.lat}
                                    num={value.id}
                                    title={value.name}
                                    name={value.name}
                                    background={value.background}
                                    description={value.description}
                                    onClick={this.markerClick}
                                    position={
                                        {lat: value.lat,
                                        lng: value.lon}
                                    }
                                    icon={{
                                        url: pivo
                                    }}
                                />
                            )}
                        </Map>
                        <div onClick={() => {$('#layout').removeClass('active')}} id="layout">
                            <div id="layoutBG" style={{backgroundImage: 'url('+this.state.points[this.state.activeTab].background+')', width: '100%', height: '30vh', backgroundAttachment: 'scroll', backgroundClip: 'border-box', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', backgroundSize: 'cover'}}/>
                            <Header className="layoutName">{this.state.points[this.state.activeTab].name}</Header>
                            <SimpleCell before={<Icon24Info />}>{this.state.points[this.state.activeTab].description}</SimpleCell>
                            {/*<Div className="layoutDescription"></Div>*/}
                            <SimpleCell before={<Avatar size={48} src={this.state.activeTabOwner.photo_200} />} description="Владелец">{this.state.activeTabOwner.first_name+' '+this.state.activeTabOwner.last_name}</SimpleCell>
                            <SimpleCell onClick={this.kupitRabotyag} multiline={true} before={<img src={pivo} alt="певко" width={24} height={24}/>} expandable={true}>Работяг можно перекупить за {this.state.points[this.state.activeTab].value} пива</SimpleCell>
                        </div>
                        {this.state.snackbar}
                    </Panel>
                </View>
            </Root>
        );
    }
}
export default GoogleApiWrapper({
    apiKey: ("AIzaSyC2L8vPYtDn5Csq-7Gj9sZqSzvb3Bx1QfI")
})(App);
